import DocsManager from "./DocsManager";

const blacklisted = new Set(["docs","master"]);

export default new DocsManager({
    id: "main",
    name: "Main",
    global: "GCommands",
    repo: "Garlic-Team/GCommands",
    defaultTag: "main",
    docsBranch: "docs/docs",
    branchFilter: (branch) => !blacklisted.has(branch)
});
